const tailwindColors = require("tailwindcss/colors");

interface ColorDepth {
  50: "#fef2f2";
  100: "#fee2e2";
  200: "#fecaca";
  300: "#fca5a5";
  400: "#f87171";
  500: "#ef4444";
  600: "#dc2626";
  700: "#b91c1c";
  800: "#991b1b";
  900: "#7f1d1d";
  950: "#450a0a";
}

export const colors = {
  primary: "#eab308",
  secondary: "#fde047",
  disabled: "#d4d4d8",
};

export const getBgColors = () => {
  console.log(tailwindColors);
  const res = Object.entries(tailwindColors).map((color) => {
    console.log(color);
    const res = color[1].map((nb) => {
      return `bg-${color[0]}-${nb}`;
    });
    return res;
  });
};
